html {
  height: 100%;
  width: 100%;
  margin: 0 auto;
  font-size: 16px;
  font-family: 'Inter', Helvetica, sans-serif;
}

body {
  height: 100%;
  width: 100%;
  margin: 0 auto;
}

#root {
  height: 100%;
  width: 100%;
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
p,
a {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  text-decoration: none;
}

img {
  display: block;
}